import React, { Component } from "react";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Maincontent from '../components/Maincontent';
import '../components/main.css';
import RoundedImage from "react-rounded-image";
import manimage from '../assets/images/manimage.png';
import womanimage from '../assets/images/womanimage.png';
import Image from 'react-bootstrap/Image';
import home from '../assets/images/home.png';




function Home(props) {

    return(
        <div >
            <Maincontent title={props.title} subTitle={props.subTitle} />
            <Image src={home} className="homeimagestyle" />


            <Container  style={{marginTop:"50px"}}>
                <Row className="RoundedImage justify-content-center" >

                 <Col  xl={3} lg={3} md={6} sm={8} xs={8}  >
                 <RoundedImage image={manimage} 
                  roundedColor="#321124"
                  imageWidth="150"
                  imageHeight="150"
                  roundedSize="5"/>
                        <h5 className="h5homestyle"> Name</h5>
                  </Col>

                  
                <Col  xl={3} lg={3} md={6} sm={8} xs={8} >
                <RoundedImage image={womanimage} 
                  roundedColor="#321124"
                  imageWidth="150"
                  imageHeight="150"
                  roundedSize="5"/>
                        <h5 className="h5homestyle"> Name</h5>
                    </Col>



                <Col  xl={3} lg={3} md={6} sm={8} xs={8} >
               <RoundedImage image={manimage} 
                  roundedColor="#321124"
                  imageWidth="150"
                  imageHeight="150"
                  roundedSize="5"  />
                    <h5 className="h5homestyle" > Name</h5>
                     </Col>

            <Col  xl={3} lg={3} md={6} sm={88} xs={8} >
            <RoundedImage image={womanimage} 
             roundedColor="#321124"
             imageWidth="150"
            imageHeight="150"
             roundedSize="5"/>
            <h5 className="h5homestyle"> Name</h5>
             </Col> 


            </Row>

                <p className="HomeText">"Lorem Ipsum sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque 
                laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi 
                architecto beatae vitae dicta sunt explicabo. Nemo enim ipsam voluptatem quia voluptas sit
                aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem
                sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, 
                adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam 
                aliquam quaerat voluptatem. Ut enim ad minima veniam, quis nostrum exercitationem ullam 
                corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur? Quis autem vel 
                eum iure reprehenderit qui in ea voluptate velit esse quam nihil molestiae consequatur,
                     vel illum qui dolorem eum fugiat quo voluptas nulla pariatur?"</p>
            </Container>

        </div>

    );

}

export default Home