import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import Maincontent from '../components/Maincontent';
import Image from 'react-bootstrap/Image';
import Button from 'react-bootstrap/Button';
import Collapse from 'react-bootstrap/Collapse';
import '../components/main.css';
import Question from '../assets/images/Questions.png';




  function Questions(props) {
  const [open, setOpen] = useState(false);

    return(
        <div>
            <Maincontent title={props.title} subTitle={props.subTitle}  />
            <Image src={Question} className="homeimagestyle" />

                <div className="questionrectangle "
                    onClick={() => setOpen(!open)}
                    aria-controls="example-collapse-text"
                    aria-expanded={open}
                >
                    <p className="questiontext "> Question 1 ?</p> 
      </div>
                <Collapse in={open}>
                    <div className="questionanswer">
                     Anim pariatur cliche reprehenderit, enim eiusmod high life accusamus
                        terry richardson ad squid. Nihil anim keffiyeh helvetica, craft beer
                        labore wes anderson cred nesciunt sapiente ea proident. 
                        
        </div>
                </Collapse>



            
            <div className="footerspace" > </div>

        </div>


    );

}

export default Questions