import React from 'react';
import {BrowserRouter as Router,Route,Link} from "react-router-dom";
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Container from 'react-bootstrap/Container';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Footer from './components/footer';
import Home from './Pages/Home';
import Researches from './Pages/Researches';
import Articles from './Pages/Articles';
import Videos from './Pages/Videos';
import Books from './Pages/Books';
import Links from './Pages/Links';
import Questions from './Pages/Questions';
import Whoarewe from './Pages/Whoarewe';


class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      title:'Dr.Mohammad Abo Hilal',

      Home:{
        title:'Something goes here about home page',
        subTitle:'Our Team ',
      },

      Researches:{
        title:'Check out our latest researches ',
        subTitle:'Researches ',
      },

      Articles:{
        title:'Check out Articles...',
        subTitle:'articles ',
      },

                Videos:{
                    title:'Watch interseting Videos about...',
                    subTitle:'videos ',
                  },

                  Books:{
                    title:'Check out the availble books',
                    text:'Click on the photos below to see more details!',

                  },


                  Links:{
                    title:'Check out links...',
                    subTitle:'Links!',

                  },
                  Questions:{
                    title:'FAQ...',
                    subTitle:'Find out answers for your questions !',

                  },
                  
                  Whoarewe:{
                    title:'Get to know more about us ',
                    subTitle:'Read  more about us ',

                  },
    }
  }

  render(){
  return (
    <Router>
      <Container className="p-0" fluid={true}>
      <Navbar  bg ="light"  expand="lg"> 
        <Navbar.Brand>Dr.Mohammad Abo Hilal</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />

        <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="mr-auto">
               <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/Researches">Researches</Link>
                <Link className="nav-link" to="/Articles">Articles</Link>
                <Link className="nav-link" to="/Videos">Videos</Link>
                <Link className="nav-link" to="/Books">Books</Link>
                <Link className="nav-link" to="/links">links</Link>
                <Link className="nav-link" to="/Questions">Questions</Link>
                <Link className="nav-link" to="/Whoarewe">Who Are We</Link>

            </Nav>
            
     <Form inline>
      <FormControl type="text" placeholder="Search" className="mr-sm-2" />
      <Button variant="outline-success">Search</Button>
    </Form>
          </Navbar.Collapse>
          </Navbar>

          <Route path="/" exact render={() => <Home title={this.state.Home.title} subTitle={this.state.Home.subTitle} text={this.state.Home.text} />} />
          <Route path="/Researches" exact render={() => <Researches title={this.state.Researches.title} subTitle={this.state.Researches.subTitle} text={this.state.Researches.text} />} />
          <Route path="/Articles" exact render={() => <Articles title={this.state.Articles.title} subTitle={this.state.Articles.subTitle} text={this.state.Articles.text} />} />
          <Route path="/Videos" exact render={() => <Videos title={this.state.Videos.title} subTitle={this.state.Videos.subTitle} text={this.state.Videos.text} />} />
          <Route path="/Books" exact render={() => <Books title={this.state.Books.title} subTitle={this.state.Books.subTitle} text={this.state.Books.text} />} />
          <Route path="/Links" exact render={() => <Links title={this.state.Links.title} subTitle={this.state.Links.subTitle} text={this.state.Links.text} />} />
          <Route path="/Questions" exact render={() => <Questions title={this.state.Questions.title} subTitle={this.state.Questions.subTitle} text={this.state.Questions.text} />} />
          <Route path="/Whoarewe" exact render={() => <Whoarewe title={this.state.Whoarewe.title} subTitle={this.state.Whoarewe.subTitle} text={this.state.Whoarewe.text} />} />


<Footer/>

      </Container>
    </Router>

  );
}
}
export default App;
